import React, { useState, Fragment, useEffect } from "react";
import shordid from "shortid";
import data from "./img/Images";
import Item from "./components/Item";

function App() {
  const [imgList, setImgList] = useState([]);

  useEffect(() => {
    setImgList(ImgaFunction);
  }, [setImgList]);

  return (
    <Fragment>
      <div className="container">
        <h1>hola Mai</h1>
        <div className="grid-container">
          {imgList.map((img, key) => (
            <Item key={key} img={img} />
          ))}
        </div>
      </div>
    </Fragment>
  );
}

function ImgaFunction() {
  data.map((e) => (e.id = shordid.generate()));
  return data;
}

export default App;
