import React, { useState } from "react";

function Item({ img }) {
  const [style, setStyle] = useState(false);
  const onClickx = () => {
    setStyle(!style);
  };
  return (
    <div>
      <img
        src={img.url}
        className={
          style ? "grid-item grid-item-1 onClick" : "grid-item grid-item-1"
        }
        onClick={onClickx}
        alt=""
      />
      <p> {img.description}</p>
    </div>
  );
}

export default Item;
